.header {
    background-color: black;
    width: 100%;
    
    &-container {
        display: flex;
        justify-content: space-between;
        margin: 0 auto;
        padding: 40px 0;
        max-width: 1300px;
        border: 2px solid black;
    }

    &__logo {
        width: 147px;
        height: 20px;
    }

    &-nav {
        font-size: 1.2rem;
        // нижнее свойство необходимо для того,
        // чтобы отображался обычный курсор между кнопок навигации.
        cursor: default;
    }

    &-nav__item {
        padding: 10px 40px;
        color: white;
    }
}

// 1-я, главная секция на чёрном фоне
.redesign {
    width: 100%;
    background-color: black;

    &-container {
        margin: 0 auto;
        max-width: 1300px;
    }

    &-cover__image {
        position: absolute;
        margin-top: 100px;
        left: 55%;
        transform: translateX(-55%);
        width: 444px;
        height: 398px;
    }

    .redesign-second__img {
        margin-left: -3%;
    }

    &-title {
        position: relative;
        margin: 0 auto;
        padding-top: 120px;
    }

    &-title__abovetitle {
        padding-left: 180px;
        font-size: 1.1rem;
        color: #999999;
    }

    &-title__item {
        color: white;
        font-size: 11rem;
    }

    .title__item1 {
        padding-left: 130px;
    }

    .title__item2 {
        padding-left: 330px;
    }

    &-descriptions {
        display: flex;
        justify-content: space-between;
        padding: 200px 50px 80px;
        font-size: 1.9rem;
        line-height: 3rem;
        color: white;
    }

    &-descriptions__item {
        max-width: 500px;
    }

    &-descriptions__free {
        color: rgba(white, 0.4);
    }

}

// 2-я секция на белом фоне "Jak możemy pomóc"
.help {
    width: 100%;

    &-container {
        margin: 0 auto;
        max-width: 1300px;
    }

    &__title {
        padding: 90px 90px 0;
        color: #E8244F;
        font-size: 1.5rem;
    }

    &-list {
        padding: 60px 0;
    }

    &-list-row {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 20px 90px;
    }

    &-list__item {
        margin: auto 0;
        min-width: 320px;
    }

    &-list__item1 {
        font-size: 4.6rem;
    }

    &-list__item2 {
        text-align: left;
        font-size: 1.6rem;
    }

    &-list__item3 {
        margin: auto 0;
        width: 43px;
        height: 43px;
    }
}

// 3-я грид-секция на белом фоне с фото
.history {
    width: 100%;

    &-container {
        display: grid;
        grid-template-rows: repeat(4, 1fr);
        grid-template-columns: repeat(4, 1fr);
        grid-template-areas:"title title title phot1" 
                            "descr descr phot2 phot3"
                            " .    phot4 phot5 phot6"
                            "phot7 phot8 after after";
        
        grid-row-gap: 20px;
        max-width: 1300px;
        margin: 0 auto;
        padding: 100px 0;
    }

    &__title {
        grid-area: title;
        font-size: 11.4rem;
        max-width: 500px;
    }

    &-descriptions {
        grid-area: descr;
        margin: auto 0;
        font-size: 2rem;
        font-size: 1.7rem;
    }

    &-descriptions__item {
        padding-bottom: 20px;
        font-size: 1.7rem;
        }

    &__photo1 {
        grid-area: phot1;
    }

    &__photo2 {
        grid-area: phot2;
    }

    &__photo3 {
        grid-area: phot3;
    }

    &__photo4 {
        grid-area: phot4;
    }

    &__photo5 {
        grid-area: phot5;
    }

    &__photo6 {
        grid-area: phot6;
    }

    &__photo7 {
        grid-area: phot7;
    }

    &__photo8 {
        grid-area: phot8;
    }

    &__afterwords {
        grid-area: after;
        margin: auto;
        font-size: 1.7rem;
    }
}

// 4-я секция на белом фоне
.trusted {
    width: 100%;

    &-container {
        max-width: 1300px;
        margin: 0 auto;
    }

    &__title {
        padding: 90px 0 0;
        color: #E8244F;
        font-size: 1.5rem;
    }

    &-firms {
        display: flex;
        flex-wrap: wrap;
        padding: 60px 0px 120px;
    }

    &-firms__item {
        width: 253px;
        height: 76px;
    }
}



// 5-я секция на чёрном фоне
.agile {

    width: 100%;
    background-color: black;

    &-container {
        display: grid;
        grid-template-rows: repeat(3, 1fr);
        grid-template-columns: repeat(3, 1fr);
        grid-template-areas: "ag__nav ag__tit ag__tit "
                             "ag__nav ag__des .       "
                             "ag__tic ag__tic ag__tic ";

        grid-row-gap: 50px;
        max-width: 1000px;
        margin: 0 auto;
        padding: 100px 0;
        color: white;
    }

    &-navigation {
        grid-area: ag__nav;
        max-width: 320px;
    }

    &-navigation-list {
        font-size: 1.4rem;
    }

    &-navigation-list__item {
        padding-top: 10px;
    }

    &-navigation-list__item:first-child {
        color: #E8244F;
    }

    &-title {
        grid-area: ag__tit;
        max-width: 500px;
    }

    &-title__item {
        font-size: 5.8rem;
    }

    &-description {        
        grid-area: ag__des;
        max-width: 420px;
    }

    &-description__item {
        padding: 20px 0;
        font-size: 2rem;
    }

    &-description__afterwords {
        font-size: 1.6rem;
        padding-bottom: 10px;
        border-bottom: 1px solid white;
    }

    &-ticker {
        // запрет переноса на новую строку
        white-space: nowrap;
        overflow: hidden;
        grid-area: ag__tic;
    }

    &-ticker__item {
        font-size: 11.4rem;
        padding-right: 50px;
        color: #181818;
    }

    &-ticker__item:first-child {
        color: #E8244F;
    }
}

// 6-я грид-секция на белом фоне
.inspiracje {

    width: 100%;

    &-container {
        max-width: 1300px;
        margin: 0 auto;
        padding: 120px 0;
        display: grid;
        grid-template-rows: repeat(3, 1fr);
        grid-template-columns: repeat(4, 1fr);
        grid-template-areas: "in__phot1 in__title in__title in__title"
                             "in__phot2 in__phot3 in__phot4 in__phot5"
                             "in__after in__after in__phot6 in__phot7";
        grid-row-gap: 40px;
    }

    &-title {
        margin: auto 0;
        grid-area: in__title;
    }

    &-title__item {
        font-size: 11.4rem;
    }

    &-title__description {
        padding: 20px  200px 0 0;
        font-size: 2rem;
    }

    &__photo1 {
        grid-area: in__phot1;
    }

    &__photo2 {
        grid-area: in__phot2;
    }

    &__photo3 {
        grid-area: in__phot3;
    }

    &__photo4 {
        grid-area: in__phot4;
    }

    &__photo5 {
        grid-area: in__phot5;
    }

    &__photo6 {
        grid-area: in__phot6;
    }

    &__photo7 {
        grid-area: in__phot7;
    }

    &__afterwords {
        margin: auto 0;
        padding-right: 140px;
        grid-area: in__after;
        font-size: 2rem;
    }
}

// 7-я секция с бегущей строкой
.ticker {

    width: 100%;

    &-container {
        margin: 0 auto;
        padding: 100px 0;
        max-width: 1300px;
        overflow: hidden;
        font-size: 11.4rem;
    }

    &__string1, 
    &__string2,
    &__string3 {
        white-space: nowrap;
    }

    &__stringGray {
        color: #888888;
    }
    &__stringBlack {
        color: black;
    }
}



.pomagamy {

    width: 100%;

    &-container{
        display: flex;
        max-width: 1300px;
        margin: 0 auto;
        padding: 100px 0;
    }

    &-cell {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin: 0 40px;
    }
    &-cell__title {
        max-width: 520px;
        font-size: 4.6rem;
    }

    &-cell__description {
        max-width: 500px;
        font-size: 2rem;
    }

    &-cell-signatures {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        max-width: 480px;
        
    }



}



